<template>
  <v-container>
    <v-card class="mt-10 pa-4" elevation="12">
      <v-card-title>
        몬스터 입력

        <v-col>
          <v-row>
            <v-col cols="3">
              <v-text-field
                ref="nameInput"
                v-model="name"
                label="몬스터 이름"
              ></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                v-model="initiative"
                label="우선권 보정치"
              ></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field v-model="ac" label="AC"></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                v-model="hp"
                label="HP"
                @keyup.enter="enter"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
      </v-card-title>

      <v-row>
        <v-col cols="12">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">몬스터 이름</th>
                  <th class="text-left">AC</th>
                  <th class="text-left">우선권</th>
                  <th class="text-left">HP</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in monsters" :key="item.name">
                  <td>{{ item.name }}</td>
                  <td>{{ item.ac }}</td>
                  <td>{{ item.initiative }}</td>
                  <td>{{ item.hp }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "MonsterStatusInsertCmp",

  data() {
    return {
      monsters: [], // 초기에 하나의 몬스터를 갖고 시작
      name: "",
      ac: "",
      hp: "",
      initiative: "",
    };
  },
  methods: {
    ...mapActions("monster", ["saveMonsters"]),
    enter() {
      const data = {
        type: "m",
        name: this.name,
        ac: this.ac,
        hp: this.hp,
        hp_tmp: "",
        initiative: this.initiative,
        initiative_tmp: "",
      };

      this.monsters.push(data);

      this.saveMonsters(this.monsters);

      this.init_input();

      this.$refs.nameInput.focus(); // 입력 필드로 포커스 이동
    },
    addMonsterRow() {
      // + 버튼을 누를 때마다 새로운 몬스터 행을 추가
      this.monsters.push({ name: "", initiative: "", ac: "", hp: "" });
    },
    init_input() {
      this.name = "";
      this.ac = "";
      this.hp = "";
      this.initiative = "";
    },
  },
  computed: {
    ...mapGetters("monster", ["getMonsters"]),
  },
  created() {
    this.monsters = this.getMonsters;
  },
};
</script>
